import React from 'react';
import { Container } from 'react-bootstrap'
import { GameBoard } from '../TicTacToeStyled'
import Board from './Board';

const calculateWinner = (squares, setIsWin, setWhoWon, moves, setMoves) => {
  const winnerLines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];

  for (let i = 0; i < winnerLines.length; i++) {
    const [a, b, c] = winnerLines[i];
    if (squares[a] && squares[a] === squares[b] && squares[b] === squares[c]) {
      setWhoWon("Wygrał: " + squares[a]);
      if (squares[a] === "X") {
        setIsWin(true)
      }
      return true;
    }

  }
  if (moves === 4) {
    setMoves(10)
    setIsWin(true)
    setWhoWon('REMISIK');
    return true;
  }
  return null;
};

const Game = ({ square, setSquare, moves, setMoves, setIsWin, setWhoWon, whoWon }) => {

  const handleClick = (i) => {
    setMoves(moves + 1)
    const squares = square.slice();
    squares[i] = "X"
    setSquare(squares);
    if (calculateWinner(squares, setIsWin, setWhoWon, moves, setMoves)) {
      return;
    };
    let flag = true;
    while (flag && moves < 4) {
      let a = Math.floor(Math.random() * 9);
      if (squares[a] === null) {
        squares[a] = "O";
        flag = false;
      }
    }
    if (calculateWinner(squares, setIsWin, setWhoWon, moves)) {
      return;
    };
  }

  return (
    <Container>
      <GameBoard>
        <Board onClick={(i) => handleClick(i)} squares={square} />
      </GameBoard>
    </Container>
  )
}

export default Game
