import React from 'react'

const Popup = ({ handleRestart, whoWon }) => {

  return (
    <div className="popup-container" style={whoWon !== '' ? { display: 'flex' } : {}}>
      <div className="popup">
        <h2>{whoWon}</h2>
        <button onClick={handleRestart}>Play Again</button>
      </div>
    </div>
  )
}

export default Popup
